export const getURL = (callback) => {
  const xhr = new XMLHttpRequest();
  xhr.responseType = "blob";
  xhr.withCredentials = true;
  xhr.open(
    "GET",
    "https://www.googleapis.com/drive/v3/files/1tjLEJ2FOgv7_s-PVBKq2Ea9xklbtJu33?alt=media"
  );
  xhr.setRequestHeader(
    "Authorization",
    "Bearer ya29.a0AVvZVsobQRvdVjm6Dt3SS0EFCnTBtSN47dVytocDnWwgwMblO2ftLozOj0X24jpTbu9SxE2-zDy1hL0UCpg5HnLKsUbvSx6l7tPSW91X4b43gy28tLboBSFK5ZQYcyNyCOHZs_1bIaWX2TeCyWNmuL69uCs6aCgYKAWESARESFQGbdwaI-lV7RMYSUYzBwQhio0ixfg0163"
  );

  xhr.onload = (event) => {
    const blob = xhr.response;

    callback(URL.createObjectURL(blob));
  };
  xhr.send();
};
